import { hot } from 'react-hot-loader';
import Page from '../components/Page';
import { LinkExternal } from '../components/Link';
import { Container, Header, Segment, Button, Icon, Image } from 'semantic-ui-react';

import { useMediaPredicate } from 'react-media-hook';
import { mobileBreakpoint } from '../helpers/Media';
import { soc } from '../configs';



function ComingSoonPage() {
  const isMobile = useMediaPredicate(mobileBreakpoint);

  return (
    <Page title={'Coming Soon'}>
      <Segment
        id='#wmc-header'
        //inverted
        textAlign='center'
        style={isMobile ? { minHeight: 350, padding: '1em 0em' } : { minHeight: 700, padding: '1em 0em' }}
        vertical
      >
        <Container style={{ paddingTop: '30px', }} textAlign='center' >
          <Image
            centered bordered rounded size='large'
            src='/summary_large_image.jpg'
          />
          <Header as='h1' textAlign='center'
            // inverted
            style={{
              fontSize: isMobile ? '2em' : '4em',
              fontWeight: 'normal',
              marginBottom: 0,
              //marginTop: isMobile ? '1.5em' : '3em',
            }}
            content='Coming soon'
          />
          <Button
            size='huge' primary icon='twitter'
            as={LinkExternal} href={soc.twitter}
          >
            Follow Us <Icon name='twitter' fitted />
          </Button>
        </Container>
      </Segment>
    </Page>
  );
};

export default hot(module)(ComingSoonPage);
